/** Page Setup */
* {
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

/** Chat Page */
.chats-page {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}

.nav-bar {
  width: 100%;
  height: 66px;
  background-color: #001455;
}

.logo-tab {
  position: absolute;
  left: 22px;
  top: 12px;
  font-size: 32px;
  font-weight: 700;
  color: #fff;
}

.logout-tab {
  position: absolute;
  top: 22px;
  right: 22px;
  color: #fff;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

/** Login Page */
#login-page {
  background-image: linear-gradient(#45aafd, #0b6dd4);
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}

#login-card {
  position: relative;
  top: calc(50vh - 144px);
  left: calc(50vw - 210px);
  padding-top: 36px;
  padding-bottom: 66px;
  width: 500px;
  text-align: center;
  background-color: #fff;
  border-radius: 22px;
}

h4 {
  color: rgba(0, 0, 0, 0.5);
}

.login-button {
  cursor: pointer;
  color: #fff;
  padding: 12px;
  border-radius: 8px;
  display: inline-block;
}

.facebook {
  background-color: #385594;
}

.google {
  background-color: #4587f1;
  color: #fff;
}

#loading-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgba(255, 255, 255, 0.2);
}

#loading-icon {
  font-size: 66px;
  position: relative;
  left: calc(50vw - 33px);
  top: calc(50vh - 22px);
}
